
.form {
  padding: ($gutter * 1.5) 0;

  background-color: $base-color;

  .container {
    grid-gap: $gutter;
    grid-template-columns: repeat(3, 1fr);
  }

  .btn-accent {
    width: 100%;
    margin: 0;

    color: $light-color;
    border-color: rgba($light-color, 0);
    border-bottom-color: rgba($light-color, 0.25);
    background-color: rgba($light-color, 0.15);

    &:hover,
    &:focus {
      cursor: pointer;

      border-color: $light-color;
      background-color: $accent-color;
    }
  }

  input {
    display: block;

    width: 100%;
    height: 60px;
    padding-right: $gutter;
    padding-left: $gutter;

    transition: background-color 0.5s, border-color 0.5s;
    letter-spacing: 0.15em;

    color: $light-color;
    border: 2px solid rgba($light-color, 0);
    border-bottom-color: rgba($light-color, 0.25);
    background-color: rgba($light-color, 0.05);

    font-size: 1.125rem;

    &:hover,
    &:focus {
      border-color: rgba($light-color, 0.15);
      border-bottom-color: $light-color;
      outline: none;
      background-color: rgba($light-color, 0.15);

      &::placeholder {
        color: lighten($gray-color, 15%);
      }
    }
  }
}
